@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');



*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Outfit', sans-serif;
    transition: all .2s linear;
    outline: none;
}
html{
    font-size: 62.5%;
    scroll-behavior: smooth;
}
body{
    overflow-x: hidden;
    width: 100%;
    height: 100%;
    font-family: 'Poppins', sans-serif;
    // background-color: rgb(56, 52, 52);
}
h1{
    font-size: 3.5rem;
    @media screen and (max-width: 968px) {
        font-size: 2.5rem;
    }
}
h2{
    font-size: 2rem;
    @media screen and (max-width: 968px) {
        font-size: 1.5rem;
    }
}
ul{
    list-style-type: none;
}
a{
    text-decoration: none;
}

.container{
    max-width: 124rem;
    margin: auto;
    padding: 0 1rem;
}

button{
    background-image: linear-gradient(45deg, (#5651e5)0%, (#709dff)100%);
    border: .1rem solid #5651e5;
    color: #fff;
    padding: .7rem 1.5rem;
    border-radius: .8rem;
    cursor: pointer;
    font-family: "Poppins", sans-serif;
}

.header{
    .navbar{
        display: flex;
        justify-content: space-between!important;
        // flex-wrap: nowrap;
        position: absolute;
        height: 8rem;
        width: 100%;
        align-items: center;
        z-index: 2;
        .logo-text{
            padding-left: 2rem;
            color: #fff;
            font-size: 2.6rem;
        }
        .dark{
            color: #000;
            padding-left: 2rem;
            font-size: 2.6rem;
        }
        .nav-menu{
           display: flex;
           padding-top: 1.4rem;
           @media screen and (max-width: 968px) {
               display: none;
           }
           .nav-item{
            padding: 0 2rem;
               a{                   
               color: #fff;
               font-size: 2rem;
               }
           }
        }
        .search{
            padding-right: 2rem;
            i{
                cursor: pointer;
                padding-left: 2rem;
                color: #fff;
                font-size: 2rem;
            }
            @media screen and (max-width:968px) {
                display: none;
            }
        }
        .mobile-menu{
            display: none;
            @media screen and (max-width: 968px) {
                    display: flex;
                    flex-direction: column;
                    position: absolute;
                    background-color: #f5f5f5;
                    width: 100%;
                    top: -100vh;
                    left: 0;
                    transition: 0.3s;  
                    .mobile-nav{
                        margin: 1rem 0;
                        li{
                            padding: 1.2rem 1rem;
                            margin: 0 1rem;
                            border-bottom: .1rem solid #ccc;
                            a{
                                color: #000;
                                font-size: 2rem;
                            }
                        }
                    }              
                    .social-icons{
                        display: flex;
                        justify-content: space-around;
                        margin: 2rem 0;
                        .icons{
                            font-size: 2rem;
                            &:hover{
                                transform: scale(1.5);
                                transition: .3s;
                            }
                        }
                    }
                    button{
                        margin: 1rem;
                        width: 90%;
                        font-size: 1.7rem;
                    }
            }
            .mobile-menu-button{
                display: none;
                @media screen and (max-width: 968px) {
                display: block;    
                width: 100%;
                text-align: center;
                padding: 1rem 0;
                }
            }
        }
        .mobile-menu.active{
            top: 7.6rem;
        }
    }
    .navbar-bg{
        background-color: #f5f5f5;
    }
    .icon{
        display: none;
        @media screen and (max-width:968px) {
            display: block;
            color: #fff;
            font-size: 2.5rem;
            justify-content: flex-end;
            padding-right: 2.5rem;

            .icon-dark{
                color: red;
                font-size: 3rem;
            }
        }
    }
}


// HOME......................................................................................




.hero{
    width: 100%;
    height: 100vh;
    color: #fff;
    position: relative;
    #video{
        // margin-top: -6.3rem;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -1;
    }
    .overlay{
        background-color: rgba(0,0,0,0.1);
        position: absolute;
        // margin-top: -6.3rem;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
    .content{
        position: absolute;
        top: 0;
        height: 100%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        width: 100%;
        margin: auto;
        padding: 1rem;
        h1{
            font-size: 6.5rem;
            font-weight: 800;
            @media screen and (max-width: 900px) {
                font-size: 5.5rem;
            }
            @media screen and (max-width: 575px) {
                font-size: 4.5rem;
            }
            @media screen and (max-width: 452px) {
                font-size: 4rem;
            }
            @media screen and (max-width: 405px) {
                font-size: 3rem;
            }
        }
        h2{
            font-size: 4rem;
            font-weight: 600;
            @media screen and (max-width: 900px) {
                font-size: 3rem;
            }
            @media screen and (max-width: 575px) {
                font-size: 2.5rem;
            }
            @media screen and (max-width: 405px) {
                font-size: 2rem;
            }
        }
        .form{
            display: flex;
            background-color: #ffffffcc;
            color: #000;
            justify-content: space-between;
            align-items: center;
            border: .1rem solid #ccc;
            padding: .6rem;
            border-radius: .8rem;
            max-width: 70rem;
            width: 100%;
            margin: 0 auto;
            @media screen and (max-width: 968px) {
                max-width: 65rem;
            }
            input{
                border: transparent;
                background-color: transparent;
                width: 40rem;
                font-size: 1.9rem;
                font-family: "Popins", sans-serif;
                // &:focus{
                //     outline: none;
                // }
            }
            button{
                i{
                     font-size: 1.6rem;
                 }
            }
        }
    }
}


// DESTINATIONS...................................................................................



.destinations{
    width: 100%;
    text-align: center;
    padding: 4rem 0;
    h1{
        padding: 1rem;
        font-size: 5rem;
        font-weight: 600;
    }
    p{
        font-size: 2.5rem;
    }
    .img-container{
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        margin: auto;
        padding: 1rem 0;
        gap: 1rem;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        .span-3{
            grid-column: span 3;
            @media screen and (max-width: 968px) {
                grid-column: span 2;
            }
        }
        .image-grid-row-2{
            grid-row: span 2;
        }
        @media screen and (max-width: 968px) {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}


// // SEARCH..........................................................



.searchPage{
    width: 100%;
    padding: 3rem 0;
    .container{
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-gap: 1rem;
        padding: 0 1rem;
        @media screen and (max-width: 968px) {
            text-align: center;
        }
        .left{
            h2{
                font-size: 3rem;
                font-weight: 600;
            }
            p{
                font-size: 2rem;
            }
            .search-col-2{
                display: grid;
                grid-template-columns: 1fr 1fr;
                padding: 2rem 0;
                justify-content: center;
                align-items: center;
                .box{
                    display: flex;
                    max-width: 30.1rem;
                    align-items: center;
                    margin: auto;
                    img{
                        padding-right: 2rem;
                    }
                    h3{
                        font-size: 1.8rem;
                        font-weight: 600;
                    }
                    p{
                        font-size: 1.9rem;
                    }
                    button{
                        font-size: 1.8rem;
                    }
                }
                @media screen and (max-width: 968px) {
                    grid-template-columns: 1fr;
                }
            }
        }
        .right{
            display: flex;
            flex-direction: column;
            justify-content: center;
            .promo{
                width: 100%;
                margin: .4rem 0;
                margin-bottom: 4rem;
                padding-top: 1rem;
                border: .1rem solid #333;
                text-align: center;
                h4{
                    font-size: 2.2rem;
                    font-weight: 700;
                    color: #5651e5;
                }
                .timer{
                    padding: 1rem 0;
                    font-size: 2rem;
                }
                .offers{
                    background: #333;
                    font-size: 1.6rem;
                    color: #f5f5f5;
                    padding: .4rem 0;
                    margin-bottom: 0;
                }
            }
            @media screen and (max-width: 968px) {
                max-width: 100%;
            }
        }
        form{
            display: flex;
            flex-direction: column;
            label{
                font-size: 2rem;
            }
            .input-wrap{
                margin: .4rem;
                display: flex;
                flex-direction: column;
                input{
                    font-size: 1.7rem;
                }
            }
            input,select{
                font-size: 1rem;
                outline: none;
            }
            button{
                font-size: 2rem;
                margin-top: 1rem;
            }
            @media screen and (max-width: 968px) {
                width: 100%;
            }
        }
        .date{
            display: flex;
            @media screen and (max-width: 968px) {
                flex-direction: column;
            }
        }
        button{
            margin: .4rem;
        }
        @media screen and (max-width: 968px) {
            grid-template-columns: 1fr;
        }
    }
}



// // SELECTSIMG...........................................................................................



.selects{
    img{
        height: 100%;
        width: 100%;
    }
    .container{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 1rem;
        @media screen and (max-width: 768px) {
            grid-template-columns: 1fr;
        }
    }
}
.selects-locations{
    position: relative;
    .overlay{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.3);
    }
    p{
        position: absolute;
        bottom: 1rem;
        left: 1rem;
        color: #fff;
        font-size: 2rem;
        font-weight: 500;
    }
}


// // CAROUSEL............................................................


.carousel{
    padding: 2rem 0;
    img{
        height: 90vh;
        object-fit: cover;
    }
}
.thumbs-wrapper, .carausel-status{
    display: none;
}



// // FOOTER.................................................................


.footer{
    width: 100%;
    padding: 2rem 0;
    background-color: #333;
    color: #fff;
    h3{
        color: #5651e5;
        font-size: 3rem;
        font-weight: 600;
    }
    .top{
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
    }
    .bottom{
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
    }
    .social{
        display: flex;
        justify-content: space-between;
        width: 20rem;
    }
    .icons{
        color: #5651e5;
        font-size: 2rem;
    }
    .left{
        display: flex;
        align-items: center;
    }
    .right{
        display: flex;
        align-items: center;
    }
    ul{
        display: flex;
        @media screen and (max-width: 1265px) {
            flex-direction: column;
        }
    }
    li{
        padding: 1rem;
        font-size: 2rem;
    }
}
